import React from "react";
import Game from "./Game";
import GameBody from "./GameBody";
import GameTitle from "./GameTitle";
import "./Match.css"

function Match({score}) {
  
  return (
    <div className="match">
      {score.map(data => {
          return (
             <React.Fragment key = {data.id}>
                <Game>
                 <GameTitle title = {data.competition_name} />
                 <GameBody sche={data.scheduled} playing = {`${data.home_name} - ${data.away_name}`} score = {data.score} link = {`/match`} time ={data.time} status ={data.status} gameId ={data.id}/>
                    </Game>
             </React.Fragment>
          )
      })}
    </div>
  );
}

export default Match;


