import React from 'react'
import Sports from './Sports'
import "./LiveOption.css"
function LiveOption() {
    return (
        <div className ="live-container">
        <div className ="live-option">
            <Sports sport ="Football" active/>
            <Sports sport ="Rugby" />
            <Sports sport ="Basketball" />
            <Sports sport ="Tennis" />
            <Sports sport ="Hocky" />
            <Sports sport = "Am. football" />
            <Sports sport ="Baseball" />
            <Sports sport ="Baseball" />
            <Sports sport ="Baseball" />
        </div>
        <div className ="live-option">
            <Sports sport ="Today" active />
            <Sports sport ="Yesterday"/>
            <Sports  sport="Tomorrow"/>
        </div>
        <div className ="live-option last">
            <Sports sport="All Games" active/>
            <Sports sport="LIVE"/>
            <Sports sport="Finished"/>
            <Sports sport="Odds"/>
        </div>
        </div>
    )
}

export default LiveOption
