import React, { useEffect, useState } from "react";
import GameTitle from "./GameTitle";
import "./MatchSum.css";
import Event from "./Event";
import axios from "axios";

// import Line from "./lineup"
function MatchSum({ id }) {
  console.log(id);

  const [eve, setEve] = useState(null);
  const [line, setLine] = useState(null);

  useEffect(() => {
    axios
      .get(
        `http://livescore-api.com/api-client/scores/events.json?key=T4REZDmZYx5EdimO&secret=3ubfgYUUApSs7Od1S5FRJ2RHAWmNeJBh&id=${id}`
      )
      .then((result) => {
        console.log(result);

        setEve({ ...result });
      })
      .catch((error) => console.log(error.message));
  }, [id]);

  useEffect(() => {
    axios
      .get(
        `https://livescore-api.com/api-client/matches/lineups.json?match_id=${id}&key=T4REZDmZYx5EdimO&secret=3ubfgYUUApSs7Od1S5FRJ2RHAWmNeJBh`
      )
      .then((result) => {
        setLine({ ...result });
      })
      .catch((error) => console.log(error.message));
  }, [id]);

  console.log(eve, "event");
  console.log(line, "line");
  const event = (eve && eve.data.data.event) || [];
  const TeamInfo = (eve && eve.data.data.match )|| {};
  const HomeTeam = (line && line.data.data.lineup.home.team.name) || "";
  const AwayTeam = (line && line.data.data.lineup.away.team.name) || "";
  const HomeLine = (line && line.data.data.lineup.home.players) || [];
  const AwayLine = (line && line.data.data.lineup.away.players) || [];
console.log(event)
  return (
    <div className="summary">
      <div className="container">
        <h3>Match Summary</h3>
        <div className="game-follow">
          <GameTitle
            title={`${TeamInfo.home_name} - ${TeamInfo.away_name} (${(TeamInfo && TeamInfo.competition &&TeamInfo.competition.name) || "NA"})`}
          />
          <div className="game-info">
            <p className="score-rev">
              <b>{TeamInfo.score}</b> ({TeamInfo.ht_score})
            </p>
            {TeamInfo.status === "FINISHED" ? (
              <span className="game-time">Finished</span>
            ) : TeamInfo.status === "IN PLAY" ? (
              <p className="game-time">In Play</p>
            ) : (
              <p className="game-time">Not Started</p>
            )}
          </div>
        </div>
        <hr></hr>

        {event.map((event) => {
          return (
            <Event
              key={event.id}
              time={event.time}
              player={event.player}
              side={event.home_away}
              event={event.event}
            />
          );
        })}
        <hr></hr>
        <div className="line-up">
          <h4>Line Up</h4>
          <div className="team">
            <GameTitle title={HomeTeam + " (Home)"} />
            <div className="team-squad">
              {HomeLine.map((home) => {
                return (
                  <div className="player" key={home.id}>
                    <span className="shirt-no">{home.shirt_number}</span>
                    <span>
                      {home.substitution === "0"
                        ? home.shirt_number === "1"
                          ? home.name + " (Gk)"
                          : home.name
                        : home.name + " (Sub) "}
                    </span>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="team">
            <GameTitle title={AwayTeam + " (Away)"} />
            <div className="team-squad">
              {AwayLine.map((home) => {
                return (
                  <div className="player" key={home.id}>
                    <span className="shirt-no">{home.shirt_number}</span>
                    <span>
                      {home.substitution === "0"
                        ? home.shirt_number === "1"
                          ? home.name + " (Gk)"
                          : home.name
                        : home.name + " (Sub) "}
                    </span>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default MatchSum;





