
import React , { useEffect , useState} from 'react'
import {BrowserRouter as Router , Switch , Route} from "react-router-dom";
import Header from './Header';
import "./App.css"
import LiveOption from './LiveOption';
import Match from './Match';
import Footer from './Footer';
import axios from "axios"
 import data from './hello'
import Standing from './Standing';
// import dataa from "./Mock";
import MatchSum from './MatchSum';
// import Event from "./Events"
export   const UseContext = React.createContext();
export   const LineUpContext = React.createContext();
export   const StatContext = React.createContext();
function App() {
    const datum = data.data.match

    const [score , setScore] = useState(datum)
    const [id , setId] = useState(null)
    const [dataa , setDataa] = useState([])
    useEffect(() => {
        axios.get("http://livescore-api.com/api-client/scores/live.json?key=T4REZDmZYx5EdimO&secret=3ubfgYUUApSs7Od1S5FRJ2RHAWmNeJBh")
        .then(result => {
            setScore(result.data.data.match)
            // console.log(result)
        })
        .catch(error => console.log(error.message))

    },[])
    useEffect(() => {
        
axios.get("http://livescore-api.com/api-client/leagues/table.json?key=T4REZDmZYx5EdimO&secret=3ubfgYUUApSs7Od1S5FRJ2RHAWmNeJBh&league=25&season=2")
.then(result => {
    setDataa(result.data.data.table)
   
})
.catch(error => console.log(error.message))
    },[])


  const  buttonCLicked = (id) => {
          setId(id)
    
    }
   
  
    return (
        <div className ="app">
            <UseContext.Provider value ={buttonCLicked}>
             
          <Router>
          <Header />
              <Switch>
                  <Route exact path ="/">
                      <LiveOption />
                      <Match score={score} />
                  </Route>
                  <Route path ="/standings">
                    <Standing  dataa={dataa} />
                  </Route>
                  <Route path = "/match">
                     <MatchSum  id ={id}/>
                  </Route>
              </Switch>
              <Footer />
              </Router> 
           
              </UseContext.Provider>
        </div>
    )
}

export default App




