import React from "react";
import "./Standing.css"
function Standing({ dataa }) {
  // const datum = (dataa && dataa.data.table) || [];
//   console.log(datum);
  return (
    <div className="standing">
        <div className ="container">
      <table className ="table table-bordered table-striped table-hover">
        <tbody>
          <tr>
              <th>S/N</th>
            <th>Name</th>
            <th>GP</th>
            <th>W</th>
            <th>D</th>
            <th>L</th>
            <th>GF</th>
            <th>GA</th>
            <th>GD</th>
            <th>P</th>
          </tr>
          {dataa.map((data) => {
            return (
              <tr key = {data.team_id}>
                     <td className ={data.rank < 5 ? "green" : data.rank > 16 ? "red" : "" }>{data.rank}</td>
                <td>{data.name}</td>
                   <td>{data.matches}</td>
                    <td>{data.won}</td>
                     <td>{data.drawn}</td>
                     <td>{data.lost}</td>
                     <td>{data.goals_scored}</td>
                     <td>{data.goals_conceded}</td>
                     <td>{data.goal_diff}</td>
                     <td>{data.points}</td>
                 
              </tr>
            );
          })}
        </tbody>
      </table>
      </div>
    </div>
  );
}

export default Standing;
