import React from 'react'
import { Link } from 'react-router-dom'
import "./Sports.css";
function Sports({sport , active }) {
    return (
        <div className ="sport">
           <Link className = {active ? 'active' : 'normal'} to = "/">{sport}</Link> <span>|</span>
        </div>
    )
}

export default Sports
