import React, { useContext } from "react";
import { Link } from "react-router-dom";
import {UseContext} from "./App"
function GameBody({ time, playing, score, link, live, sche , status , gameId }) {
  const getFunc = useContext(UseContext)
  return (
    <div className="game-body">
      {time === "FT" ? (
        <span className="time">{time}</span>
      ) : 
      time === "HT" ? (<span className="time time-red">{time}</span>)
      : status === "IN PLAY" ?
      <span className ="time time-red">{time}'</span>
      : time === "AP" ?
      <span className="time">{time}</span>
      :
      time === "PEN" ?
      <span className="time time-red">{time}</span>
      :
      time === "AET" ?
      <span className="time">{time}</span>
      :
      <span className ="time">{sche}</span>

}

      <span className="playing">{playing}</span>
      <span className="score">
        { status === "IN PLAY"  ? <Link to= {link} className ="score-red" onClick = {()=>  getFunc(gameId)} >{score}</Link> : <Link to={link} onClick = {()=> getFunc(gameId)}>{score}</Link>}
      </span>
    </div>
  );
}

export default GameBody;
